.leaderboard-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 1rem;
    margin-top: 2rem;
}

.leader-card {
    background: #f8f9fa;
    border-left: 6px solid #007bff;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
}

.leader-card h3 {
    margin: 0;
    color: #007bff;
}

.leader-card p {
    margin: 0.25rem 0;
    font-size: 0.95rem;
}